<!-- eslint-disable vue/multi-word-component-names -->
<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getHomeData } from '@/api/home.js'
export default {
  data () {
    return {
      bannerList: [],
      navList: [],
      proList: [],
      isLoading: false
    }
  },
  methods: {
    onRefresh () {
      this.isLoading = true
      setTimeout(() => {
        location.reload()
        this.isLoading = false
      }, 900)
    }
  },
  async created () {
    const { data: { pageData: { items } } } = await getHomeData()
    this.bannerList = items[1].data
    this.navList = items[3].data
    this.proList = items[6].data
  },
  components: {
    GoodsItem
  }
}
</script>

<template>
  <div class="home">

    <!-- 导航条 -->
    <van-nav-bar title="智慧商城" fixed />

    <!-- 搜索框 -->
    <van-search
      readonly
      shape="round"
      background="#f1f1f2"
      placeholder="请在此输入搜索关键词"
      @click="$router.push('/search')"
    />

  <van-pull-refresh
  v-model="isLoading"
  success-text="刷新成功"
  @refresh="onRefresh"
  >
    <!-- 轮播图 -->
    <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
      <van-swipe-item v-for="i in bannerList" :key="i.imgUrl">
        <img :src="i.imgUrl">
      </van-swipe-item>
    </van-swipe>

    <!-- 导航 -->
    <van-grid column-num="5" icon-size="40">
      <van-grid-item
        v-for="i in navList" :key="i.text"
        :icon="i.imgUrl"
        :text="i.text"
        @click="$router.push('/category')"
      />
    </van-grid>

    <!-- 主会场 -->
    <div class="main">
      <img src="@/assets/main.png" alt="">
    </div>

    <!-- 猜你喜欢 -->
    <div class="guess">
      <p class="guess-title">—— 猜你喜欢 ——</p>

      <div class="goods-list">
        <GoodsItem v-for="i in proList" :key="i.goods_id" :item="i"></GoodsItem>
      </div>
    </div>
  </van-pull-refresh>
  </div>
</template>

<style lang="less" scoped>
// 主题 padding
.home {
  padding-top: 100px;
  padding-bottom: 50px;
}

// 导航条样式定制
.van-nav-bar {
  z-index: 999;
  background-color: #e8e8e8;
  ::v-deep .van-nav-bar__title {
    color: #7837cc;
  }
}

// 搜索框样式定制
.van-search {
  position: fixed;
  width: 100%;
  top: 46px;
  z-index: 999;
}

// 分类导航部分
.my-swipe .van-swipe-item {
  height: 185px;
  color: #fff;
  font-size: 20px;
  text-align: center;
  background-color: #39a9ed;
}
.my-swipe .van-swipe-item img {
  width: 100%;
  height: 185px;
}

// 主会场
.main img {
  display: block;
  width: 100%;
}

// 猜你喜欢
.guess .guess-title {
  height: 40px;
  line-height: 40px;
  text-align: center;
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
</style>
